<template>
  <v-app>
    <div>
      <v-navigation-drawer
        persistent
        :mini-variant="miniVariant"
        :clipped="clipped"
        v-model="drawer"
        enable-resize-watcher
        fixed
        app
      >
        <v-list>
          <v-list-tile @click="routerSelection('home')">
            <v-list-tile-content>
              <v-list-tile-title>Home</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-group value="true" v-for="item in items" :key="item.title">
            <template v-slot:activator>
              <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
              </v-list-tile-content>
            </template>
            <v-list-tile
              v-for="subItem in item.subItems"
              :key="subItem.title"
              v-if="subItem.role.indexOf(currentUser.role) >= 0"
              @click="routerSelection(subItem.link)"
            >
              <v-list-tile-content>
                <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-icon>{{ subItem.action }}</v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar app :clipped-left="clipped">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-toolbar-title v-text="title"></v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-title v-text="currentUser.unique_name"></v-toolbar-title>
        <v-btn flat icon color="red" @click="logout()">
          <i class="material-icons">power_settings_new</i>
        </v-btn>
      </v-toolbar>
      <v-content>
        <router-view />
      </v-content>
      <v-navigation-drawer temporary :right="right" v-model="rightDrawer" fixed app>
        <v-list>
          <v-list-tile @click="right = !right">
            <v-list-tile-action>
              <v-icon>compare_arrows</v-icon>
            </v-list-tile-action>
            <v-list-tile-title>Switch drawer (click me)</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-footer :fixed="fixed" app>
        <span>&copy; 2017 v{{ version }}</span>
      </v-footer>
    </div>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Log Out"
      message="Are you sure you want to log out?"
    ></confirmation-dialog>
  </v-app>
</template>

<script>
import Router from "./router";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import authentication from "./authentication";
//const queryString = require("query-string");

export default {
  name: "App",
  components: {
    ConfirmationDialog
  },
  data() {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
      items: [
        {
          title: "Administration",
          subItems: [
            {
              icon: "bubble_chart",
              title: "Provider",
              link: "physician-administration",
              role: ["user", "administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Provider Education",
              link: "physician-education",
              role: ["user", "administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Entity",
              link: "entity_administration",
              role: ["user", "administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Practice/Hospital Department",
              link: "practice_administration",
              role: ["administrator"]
            }
          ]
        },
        {
          title: "Audits",
          subItems: [
            {
              icon: "bubble_chart",
              title: "Start Audit",
              link: "audit-record-selection",
              role: ["hidden for now"]
            },
            {
              icon: "bubble_chart",
              title: "Create Practice/Hospital Department",
              link: "audit_start",
              role: ["idden for now"]
            },
            {
              icon: "bubble_chart",
              title: "Create 2",
              link: "audit_start_2",
              role: ["administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Create Audit for Provider",
              link: "audit_start_provider",
              role: ["user", "administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Open Audits",
              link: "audit-record-listing",
              role: ["user", "administrator"]
            },
            {
              icon: "bubble_chart",
              title: "Create Onboarding Audit",
              link: "audit_start_onboarding",
              role: ["user", "administrator"]
            }
          ]
        },
        {
          title: "Utilities",
          subItems: [
            {
              icon: "bubble_chart",
              title: "Logging",
              link: "logging",
              role: ["administrator"]
            }
          ]
        }
      ],
      dialog: false,
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: "CHSLI App",
      version: "0.9m"
    };
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    }
  },
  methods: {
    routerSelection: function(sel) {
      Router.push({ name: sel });
    },
    logout: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          authentication.signOut();
          //this.logonEvent();
        }
      });
    }
  },
  async beforeMount() {
    if (authentication.isAuthenticated()) {
      // load data needed from api
      //this.setCurrentUser(this.$store.getters.CURRENT_USER);
      await this.$store.dispatch("FETCH_USERS");
      await this.$store.dispatch("FETCH_ENTITY");
      await this.$store.dispatch("FETCH_PRACTICE");
      await this.$store.dispatch("FETCH_PHYSICIAN");
      await this.$store.dispatch("FETCH_APPLICATION_LISTS");
      await this.$store.dispatch("FETCH_SCORING_LIST");

      //      this.$store.dispatch("SET_CURRENT_USER", authentication.getUserProfile()); // excute after fetching all uses
    } else {
      authentication.signIn();
    }
  },
  mounted() {}
};
</script>
